<template>
    <div class="notice-container">
        <van-list
            v-show="!isEmpty"
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="$getNoticeLists"
        >
            <div class="notice-item bg-white" v-for="item in lists" :key="item.id">
                <div class="header row-between">
                    <div class="normal md bold">{{item.title}}</div>
                    <div class="muted xs">{{item.create_time}}</div>
                </div>
                <div class="content">
                    <div class="sm lighter">{{item.content}}</div>
                </div>
            </div>
        </van-list>
        <div class="data-null column-center" v-show="isEmpty">
            <div class="img-null">
                <img style="width: 100%; height: 100%" src="@A/images/null_notice.png"/>
            </div>
            <div class="muted nr">
                暂无消息通知～
            </div>
        </div>
    </div>
</template>

<script>
import {getNoticeLists} from "@API/store"
export default {
    name: 'notice',
    data() {
        return {
            type: 'system',
            page: 1,
            loading: true,
            finished: false,
            lists: [],
            isEmpty: false
        }
    },
    methods: {
        $getNoticeLists(type) {
            let {finished, lists} = this;
            this.loading = true;
            if(finished) return;
            getNoticeLists({type, page_no: this.page}).then(res => {
                if(res.code == 1) {
                    let {more, list} = res.data;
                    this.loading = false;
                    lists.push(...list);
                    this.lists = lists;
                    this.page ++;
                    if(!more) {
                        this.finished = true;
                    }
                    if(lists.length <= 0) {
                        this.isEmpty = true
                    }
                }
            })
        }
    },
    mounted() {
        this.type = this.$route.query.type;
        this.$getNoticeLists(this.type)
    }
}
</script>

<style lang="scss" scoped>
.notice-container {
    padding: 10px;
    .notice-item {
        padding: 10px;
        border-radius: 7px;
        .header {
            padding-bottom: 10px;
            border-bottom: 1px solid $--border-color-base;
        }
        .content {
            padding-top: 12px;
        }
    }
    .data-null {
        padding-top: 100px;
    }
}
</style>